.container {
  margin-top: 2.5rem;
}

.heading {
  font-size: 3.2rem;
  color: var(--black);
  margin: 1rem 0;
}

.inputBox {
  composes: inputBox from './filter.module.css';
  border-radius: 0.5rem;
  width: 100%;
  margin-bottom: 0.8rem;
}

.story {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.storyForm {
  width: 45%;
  margin-right: 1rem;
}

.storyPreview {
  background-color: #f8f8f8;
  padding: 1.5rem;
  width: 45%;
}

.inputArea {
  composes: inputBox;
  max-width: 100%;
  min-height: 15rem;
}

.btn {
  display: inline-block;
  padding: 1.2rem;
  border-radius: 0.5rem;
  color: #fff;
  font-size: 1.6rem;
  text-decoration: none;
  border: none;
  height: 100%;
  text-align: center;
  background-color: var(--color2);
}

@media (max-width: 800px) {
  .storyForm,
  .storyPreview {
    width: 100%;
  }

  .storyPreview {
    margin-top: 1.5rem;
  }
}



